<template>
    <div>
        <div v-if="loadding">loadding...</div>
        <div v-if="error">{{error}}...</div>
        <div v-if="post">
            <h3>标题：{{post.title}}</h3>
            <p>内容：{{post.body}}</p>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                post:'',
                error:'',
                loadding:false
            }
        },
        created () {
            //console.log(this.$http);
            this.getPostData();
        },
        watch: {
            $route:'getPostData'
        },
        methods: {
           async getPostData(){
               try {
                   this.loadding = true;
                   const {data} = await this.$http.get('/api/post');
                  this.loadding = false;
                  this.post = data;
               } catch (error) {
                   this.error = error.toString();
                   //console.log( this.error);
               }
            }
        },
    }
</script>

